}

/* Larger than desktop */
@media (min-width: 1000px) {}

/* Larger than Desktop HD */
@media (min-width: 1200px) {
    .sidenav {
        display: none;
    }

    .navbar-container {
        padding: 20px 100px;
    }

    .navbar-logo {
