.waiting-section {
    padding: 120px 25px 70px 25px;
}

.waiting-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.waiting-container .img-illustration {
    width: 120px;
    height: 184px;
    background-image: url(/static/media/waiting-illustration.b117265f.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    -o-background-size: contain;
    margin-bottom: 30px;
}

.waiting-container h1 {
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    color: #29293E;
    margin-bottom: 20px;

}

.waiting-container p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 25px;
    text-align: center;
    color: #78788C;
    margin-bottom: 50px;
}

.waiting-container .homepage-button {
    width: 100%;
    text-align: center;
}

.homepage-button button {
    background-color: #456DFC;
    background-image: linear-gradient(330.73deg, #456DFC -1.85%, #62B2FF 100%);
    border-radius: 5px;
    font-style: normal;
    text-align: center;
    font-weight: 600;
    font-size: 16px;
    cursor: pointer;
    color: #FFFFFF;
    padding: 15px 25px;
    width: 100%;
}

@media (min-width: 1200px) {
    .waiting-section {
        padding: 170px 100px 80px 100px;
    }

    .waiting-container .img-illustration {
        width: 200px;
        height: 307px;
        margin-bottom: 50px;
    }

    .waiting-container h1 {
        font-size: 30px;
        margin-bottom: 25px;

    }

    .waiting-container p {
        font-size: 20px;
        line-height: 32px;
        width: 55%;
        margin-bottom: 40px;
    }

    .waiting-container .homepage-button {
        width: auto;
    }

    .homepage-button button {
        font-weight: 600;
        font-size: 20px;
        color: #FFFFFF;
        padding: 15px 25px;
    }
}
@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@-webkit-keyframes fadein {

    /* Safari and Chrome */
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


